import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	computed: mapState(['hasLogin', 'userInfo']),
	data() {
		return {
			chatList: [],
			isLogin: false,
		}
	},
	created() {
		var accessToken = uni.getStorageSync('userInfo').accessToken;
		if (accessToken) {
			this.getChatList();
			this.isLogin = true;
		} else {

		}

	},
	methods: {
		gotoChart(myId,toId) {
			console.log(myId+"   "+toId);
			const that = this;
			that.$Router.push({
				path: "/pages/consultant/charRoom",
				query:{
					myUserId:myId,
					chatUserId:toId
				}
			})
		},
		getChatList() {
			const that = this;
			that.$api.mychatList({}).then(res => {
				if (res.success) {
					let result = res.datas.rows;
					for (var i in result) {
						result[i].chatUserHeadImg = result[i].chatUserHeadImg ? result[i].chatUserHeadImg :
							"../../static/images/defaultUserPic.png";
					}
					that.chatList = result;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
	}
}
